.wrapper {
  width: 100%;
  height: 100%;
  padding: 2rem;
  box-sizing: border-box;
  transform-origin: bottom center;
}

.content {
  max-width: 1400px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.caption {
  font-size: 0.875rem;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  margin-bottom: 1rem;
  color: #333;
}

.title {
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 1.5rem;
  color: #333;
  line-height: 1.2;
}

.title i {
  font-style: italic;
}

.grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 1rem;
  flex: 1;
}

.item {
  will-change: transform, opacity;
}

.imageContainer {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  margin-bottom: 0.5rem;
  overflow: hidden;
}

.image {
  object-fit: cover;
  transition: transform 0.5s ease;
}

.item:hover .image {
  transform: scale(1.05);
}

.label {
  font-size: 0.7rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #333;
}

@media (max-width: 1024px) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 768px) {
  .wrapper {
    padding: 1.5rem;
  }
  
  .title {
    font-size: 1.5rem;
  }
  
  .grid {
    grid-template-columns: 1fr;
  }
  
  .imageContainer {
    padding-bottom: 70%;
  }
}